/* /* Reset some basic elements for cross-browser consistency */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Arial', sans-serif;
  background-color: #f9f9f9; /* Light gray background */
  color: #333; /* Dark text color */
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.App-header {
  background-color: #ed1d24; /* Bright red */
  padding: 30px;
  color: #fff;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.App-header h1 {
  font-size: 5.9em;
  margin-top: 20px;
}


nav {
  display: flex;
}

nav ul {
  list-style-type: none;
  display: flex;
  gap: 20.9px;
}

nav li {
  display: inline;
}

nav a {
  color: #fff;
  text-decoration: none;
  font-size: 4.3em;
  padding: 10px 20px;
  transition: background-color 0.3s, color 0.3s;
}

nav a:hover {
  background-color: #fff;
  color: #ed1d24;
  border-radius: 5px;
}

main {
  flex: 1;
  padding: 150px 20px 20px;
  background-color: #fff; /* White background for the main content */
  margin: 500px auto;
  max-width: 10000px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); /* Light shadow for contrast */
  border-radius: 99px;
}

h1 {
  color: #333;
  font-size: 7em;
  margin-bottom: 40px;
}

p {
  color: #666; /* Dark gray text */
  font-size: 3.5em;
  line-height: 1.6;
}
